export default{
	data(){
		return{
			value:''
		}
	},
	onLoad() {
		this.SearchOld()
	},
	methods:{
		// 历史搜索-热门搜索
		SearchOld(){
			uni.request({
				url:'http://shop_api.fang-tian.com/api_fang/house/SearchOld',
				methods:'POST',
				data:{type:2}, 
				success(res) {
					console.log(res)
				}
			})
		},
		
		searchApi(){
			var that = this;
			uni.request({
				url:'http://shop_api.fang-tian.com/api_fang/house/searchHouse',
				methods:'POST',
				data:{
					type:1,
					search_name:this.value
				}, 
				success(res) {
					console.log(res)
					console.log(that.value)
				}
			})
		},
		historyApi(){
			var that = this;
			uni.request({
				url:'http://shop_api.fang-tian.com/api_fang/house/DelSearchOld',
				methods:'POST',
				data:{
					type:1
				}, 
				success(res) {
					console.log(res)
				}
			})
		}
	}		
}
